



















import { login } from '@/request/API/index.js';
export default {
	data() {
		return {
			password: '',
			phone: ''
		};
	},
	onLoad() {},
	methods: {
		login() {
			const reg = /^[1][3,4,5,7,8][0-9]{9}$/;
			if (!reg.test(this.phone)) {
				console.log(this.phone);
				uni.showModal({
					title: '',
					content: '请输入正确的手机号码',
					showCancel: false
				});
				return;
			}
			if (!this.password) {
				uni.showModal({
					title: '',
					content: '请输入密码',
					showCancel: false
				});
				return;
			}
			let params = {
				phone: this.phone,
				password: this.password,
				userType: 1
			};
			login(params).then(res => {
				if (res.data.code == 0) {
					console.log(res.data.data.token);
					uni.setStorageSync('token', res.data.data.token);
					uni.setStorageSync('userId', res.data.data.userId);
					uni.setStorageSync('shopId', res.data.data.shopId);
					this.$store.commit('SET_SHOPID', res.data.data.shopId);
					this.$store.commit('SET_TOKEN', res.data.data.token);
					this.$store.commit('SET_USERID', res.data.data.userId);
					uni.reLaunch({
						url: '/pages/index/index'
					});
				} else {
					uni.showModal({
						title: '',
						content: res.data.msg,
						showCancel: false
					});
				}
			});
		},
		register() {
			uni.navigateTo({
				url: '/pages/register/register'
			});
		},
		wjPassword() {
			uni.navigateTo({
				url: '/pages/wjPassword/wjPassword'
			});
		}
	}
};
